	module.exports = {"data":{"page":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/pages/1--articles-and-talks/index.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li><a href=\"https://dsconference.com/ws-intergalactico/\">Hands-on with Design Systems</a><br/>\nFull-day workshop about building design system and pattern library (React) in multidisciplinary team.</li>\n<li><a href=\"http://varya.me/component-development-css-2017/\">Component development in CSS in 2017</a><br/>\nLecture for Computer Science students of Aalto University on overall picture of developing interface\ncomponents using modern CSS component approaches.</li>\n<li><a href=\"https://www.dotconferences.com/2016/12/varya-stepanova-pattern-libraries-through-trial-and-error\">Pattern libraries through trial and\nerror</a><br/>\nThe talk given at DotCSS 2016 (Paris, France) summarize my 8-year-long experience in building pattern libraries.\n<a href=\"https://www.dotconferences.com/2016/12/varya-stepanova-pattern-libraries-through-trial-and-error\">Video</a> and\n<a href=\"http://varya.me/dotcss-2016/\">slides</a> are available.</li>\n<li><a href=\"http://varya.me/component-development-css-2016/\">Component development in CSS in 2016</a><br/>\nLecture for Computer Science students of Aalto University on overall picture of developing interface\ncomponents using CSS.</li>\n<li><a href=\"https://youtu.be/UPciXG1MoYw\">Doing nothing for visual regression testing</a><br/>\nShaped talk about applying visual regression testing approach to the projects using\nstyleguide-driven development and a show-case how it is very helpful for preventing unwanted interface changes and\nkeeping websites making sense for users. Given in Bologna, Italy. <a href=\"https://youtu.be/UPciXG1MoYw\">Video</a> and\n<a href=\"http://varya.me/fromthefront-2016/\">slides</a></li>\n<li><a href=\"http://www.didoo.net/to-bem-or-not-to-bem/05__interview-with--varya__stepanova.html\">To BEM or not to BEM</a><br/>\nInterview to Cristiano Rastelli about using BEM in developing frontend.</li>\n<li><a href=\"http://varya.me/reactjs-hackathon/\">ReactJS Hackathon @ SC5</a><br/>\nMaterials for making a hackathon on React for a beginner level.</li>\n<li><a href=\"http://varya.me/doing-nothing-for-visual-regression-2015/\">Doing nothing for visual regression testing</a><br/>\nVisual regression testing approach wrapped into a nice and neat plugin for SC5 StyleGuide presented at HelsinkiJs\nmeetup in October 2015.</li>\n<li>\n<p><a href=\"https://youtu.be/bKI0amimw-k\">Driving Style-Guide-Driven Development</a><br/>\nThis talk describes experience of brining Style-Guide-Driven Development into practise and reveals how it\nended up with developing a special tool — Style Guide Generator by SC5. The presenttaion was given at EmpireJS 2015,\nNew York and ScotlandJS 2015, Edinburgh.</p>\n<ul>\n<li><a href=\"https://youtu.be/bKI0amimw-k\">Video from EmpireJS, New York</a></li>\n<li><a href=\"https://youtu.be/gWzYMJjtx-Y\">Video from ScotlandJs, Edinburgh</a></li>\n<li><a href=\"http://varya.me/empirejs-2015/\">Slides</a></li>\n</ul>\n</li>\n<li><a href=\"http://www.smashingmagazine.com/2015/03/05/automating-style-guide-driven-development/\">Automating Style-Guide-Driven\nDevelopment</a><br/>\nThis article at <em>SmashingMagazine</em> was published on 5th March 2105. It explained the motivation and benefits\nof style-guide-driven development and component-based approach in frontend.</li>\n<li><a href=\"http://www.slideshare.net/VarvaraStepanova/sc5-style-guide-generator\">Styleguide generator by SC5</a><br/>\nWith this presentation I introduced SC5 StyleGuide to frontend developers at <a href=\"https://www.veikkaus.fi/\">Veikkaus</a>\nand <a href=\"http://elisa.fi/\">Elisa</a> in December 2014 and January 2015 and <a href=\"http://frontend.fi/\">Frontend.fi</a> on January 2015.</li>\n<li><a href=\"http://getbem.com/faq/\">BEM FAQ</a><br/>\nA list of frequently asked questions about BEM and detailed answers to them.</li>\n<li><a href=\"http://varya.me/jscs-talk/\">JSCS in use at SC5 Online</a><br/>\nThis talk given 18th on November at <a href=\"http://www.eventbrite.com/e/helsinkijs-november-2014-tickets-14262484475?utm_campaign=event_reminder&#x26;utm_medium=email&#x26;ref=eemaileventremind&#x26;utm_source=eb_email&#x26;utm_term=eventname\">HelsinkiJS\nmeetup</a>\npresents JSCS, a tool for checking JavaScript code style, and describes experience of useing it\nin <a href=\"http://sc5.io/\">SC5 Online</a>.</li>\n<li><a href=\"http://bem.info/tutorials/bem-js-tutorial/\">Tutorial on JavaScript components with\ni-bem.js</a><br/>\nThis set of well-explained examples demonstrates how to create JavaScript\ncomponents using the basic features of <code class=\"language-text\">i-bem.js</code>.</li>\n<li><a href=\"https://vimeo.com/66474705\">The thinking behind BEM</a><br/>\nThe talk at <a href=\"http://2013.front-trends.com/\">Front-Trends 2013</a>  gives a birds-eye\nview of the sometimes surprising ideas behind BEM, and the benefits they offer\ndevelopers.</li>\n<li><a href=\"http://hasgeek.tv/metarefresh/2013/496-maintainable-frontend-development-with-bem\">Maintainable Frontend Development with\nBEM</a><br/>\nThis is a talk given at <a href=\"http://metarefresh.in/2013/\">Metarefresh</a> on 23\nFebruary 2013 in\nBangalore (India). It shows how BEM helps to keep web interfaces\neasy-to-maintain and fast-to-develop.</li>\n<li><a href=\"http://bem.info/articles/start-with-project-stub/\">Quick start with full BEM\nstack</a><br/>\nThis tutorial shows how to run a BEM-project and enjoy helpful <code class=\"language-text\">bem tools</code>,\nefficient <code class=\"language-text\">BEMHTML</code> templates and declarative JavaScript <code class=\"language-text\">i-bem.js</code>.</li>\n<li><a href=\"https://vimeo.com/53219242\">What you can borrow from Yandex frontend\ndev</a><br/>\nThe talk about BEM at <a href=\"http://webconf.lv/\">WebConf Riga</a> on 10 November 2012.\nHere it’s explained at length what is BEM in terms of CSS. BEM-styled JavaScript\nis also mentioned as well as some nice tools for frontend development.<br/>\nThis content is also available as an <a href=\"http://bem.info/articles/yandex-frontend-dev/\">article “What you can borrow from Yandex frontend\ndev”</a>.</li>\n<li><a href=\"https://vimeo.com/51897014\">Frontend evolution at Yandex</a><br/>\nFull 7-year evolution of BEM in 10 minutes. This is in the talk at Fronteers Jam\nSession, October 2012 in Amsterdam.</li>\n<li><a href=\"http://coding.smashingmagazine.com/2012/04/16/a-new-front-end-methodology-bem/\">A New Frontend Methodology:\nBEM</a><br/>\nThe first article about BEM in English.</li>\n<li><a href=\"https://vimeo.com/38346573\">How to use BEM! outside Yandex</a><br/>\nSceencast based on my talk at Yandex.Saturday Conferece on 25 February 2012 in\nChelyabinsk (Russia). Says that every team can enjoy BEM and improve its work.\nInside you can find examples of using BEM with Haml and Django.</li>\n</ul>","frontmatter":{"title":"Articles and talks"}}},"pathContext":{"slug":"/articles-and-talks/"}}

